.container {
  background-color: #fff;
  width: 100%;
  min-height: 100vh;
  position: relative;
  overflow: hidden;
}

.container:before {
  content: "";
  z-index: 6;
  background: linear-gradient(-45deg, #ff4167, #e9338e, #ed66a3);
  border-radius: 50%;
  width: 2000px;
  height: 2000px;
  transition: all 1.8s ease-in-out;
  position: absolute;
  top: -10%;
  right: 48%;
  transform: translateY(-50%);
}

.form-container {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.signin-signup {
  z-index: 5;
  grid-template-columns: 1fr;
  width: 50%;
  transition: all 1s ease-in-out .7s;
  display: grid;
  position: absolute;
  top: 50%;
  left: 75%;
  transform: translate(-50%, -50%);
}

form {
  flex-direction: column;
  grid-area: 1 / 1 / 2 / 2;
  justify-content: center;
  align-items: center;
  padding: 0 5rem;
  transition: all .2s ease-in-out .7s;
  display: flex;
  overflow: hidden;
}

form.sign-in-form {
