  max-width: 380px;
  height: 55px;
  margin: 10px 0;
  padding: 0 .4rem;
  display: grid;
}

.input-field i {
  text-align: center;
  color: #acacac;
  font-size: 1.1rem;
  line-height: 55px;
}

.input-field input {
  color: #333;
  background: none;
  border: none;
  outline: none;
  font-size: 1.1rem;
  font-weight: 600;
  line-height: 1;
}

.input-field input::placeholder {
  color: #aaa;
  font-weight: 500;
}

