.panel-container {
  grid-template-columns: repeat(2, 1fr);
  width: 100%;
  height: 100%;
  display: grid;
  position: absolute;
  top: 0;
  left: 0;
}

.panel {
  text-align: center;
  z-index: 7;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-end;
  display: flex;
}

.panel .content {
  color: #fff;
  transition: all 1.1s ease-in-out .4s;
}

